.firstImagePage {
  background-size: 100% 100%;
  width: 100%;
  height: 600px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: white;

  h1 {
    font-size: 3.5rem;
    font-weight: 300;
    border-bottom: 2px solid $title-color;
    margin-bottom: 40px;
    text-align: center;
    animation-name: apparitionTitre;
    animation-duration: 1s;
    animation-delay: 0.1s;
    animation-fill-mode: both;
    opacity: 0;
  }
  h2 {
    font-size: 2rem;
    font-weight: 300;
    text-align: center;
    animation-duration: 1s;
    animation-delay: 0.1s;
    animation-name: apparitionTitre;
    opacity: 0;
    animation-fill-mode: both;
  }
  @media screen and (min-width: 800px) {
    h1 {
      font-size: 4.5rem;
    }
    h2 {
      font-size: 2rem;
    }
  }
  @media screen and (min-width: 1400px) {
    h1 {
      font-size: 6rem;
    }
    h2 {
      font-size: 3rem;
    }
  }
}

.mobile_background {
  img {
    width: 100%;
  }
  h1,
  h2 {
    text-align: center;
    margin: 20px auto;
  }
  h1 {
    color: $title-color;
  }
  h2 {
    color: $texte-color;
  }
}

@keyframes apparitionTitre {
  from {
    transform: translateY(100px);
    opacity: 0;
  }
  to {
    transform: translateY(0px);
    opacity: 1;
  }
}
