.programme_affiche_container {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 100px auto 40px;

  .affiche_container {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: 0px auto;
    padding: 20px 0px;
    justify-content: center;
    background-color: $third-background-color;
    animation-duration: 0.8s;
    animation-fill-mode: both;

    opacity: 0;
    &:nth-child(1) {
      animation: none;
      opacity: 1;
    }

    .affiche {
      width: 80%;
      margin: auto;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 20px 0px;
      img {
        width: 300px;
        height: 300px;
        margin: auto;
        box-shadow: 0 5px 10px rgba(14, 21, 47, 0.2),
          0 5px 10px rgba(14, 21, 47, 0.2);
      }
    }
    .description {
      width: 80%;
      margin: auto;
      padding-bottom: 20px;

      h2 {
        text-align: center;
        margin: 10px auto 20px;
        color: $main-color;
      }
      p {
        padding: 10px;
      }
    }
    &:nth-child(even) {
      background-color: white;

      .description {
        h2 {
          color: $main-color;
        }
        p {
          color: white;
        }
      }
    }
    // &:nth-child(1) {
    //   img {
    //     height: 420px;
    //     transform: scale(1.3);
    //   }
    // }
  }
}
@media screen and (min-width: 800px) {
  .programme_affiche_container {
    .affiche_container {
      flex-direction: row;
      width: 100%;
      justify-content: space-around;
      padding: 20px 100px;

      .affiche {
        width: 50%;
      }
      .description {
        width: 50%;
        p {
          width: 90%;
          margin: auto;
        }
      }

      &:nth-child(even) {
        .affiche {
          order: 2;
        }
        .description {
          order: 1;
        }
      }
    }
  }
}
@keyframes apparitionAffiche {
  from {
    opacity: 0;
    transform: translateY(100px);
  }
  to {
    opacity: 1;
    transform: translateY(0px);
  }
}
