.intro_programme {
  margin: 130px auto 50px;
  width: 90%;
  display: flex;
  flex-direction: column;
  justify-content: center;

  img {
    width: 300px;
    height: 420px;
    margin: auto;
    box-shadow: 0 5px 10px rgba(14, 21, 47, 0.2),
      0 5px 10px rgba(14, 21, 47, 0.2);
  }

  h1 {
    text-align: center;
    color: $title-color;
  }
  p {
    width: 90%;
    margin: 30px auto;
    text-align: justify;
    text-align: center;
  }
  a {
    display: inline-block;
    transform: translateX(7px);
    color: $main-color;
    font-weight: 600;
    font-family: $title-font-family;

    &:hover {
      color: $title-color;
    }
  }
  .liensContainer {
    width: 80%;
    margin: 0px auto 30px;

    h3 {
      text-align: center;
      margin: 10px auto 20px;
    }

    .pdf_container {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      a {
        width: 180px;
        margin: 20px 30px;
        height: 130px;
        background-size: contain;
        background-repeat: no-repeat;
        display: block;
        transition-duration: 0.3s;
        transition-property: all;
        transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);

        img {
          width: 180px;
          height: 130px;
        }

        &:hover {
          opacity: 0.8;
          transform: scale(1.05, 1.05);
        }

        // &::before {
        //   content: "";
        //   position: absolute;
        //   height: 7px;
        //   width: 7px;
        //   background-color: $title-color;
        //   margin-left: -13px;
        //   margin-top: 8px;
        //   border-radius: 50%/50%;
        // }
      }
    }
  }
}

@media screen and (min-width: 800px) {
  .intro_programme {
    width: 80%;
    img {
      width: 500px;
      height: 700px;
      margin: auto;
    }

    p {
      width: 80%;
    }
    .liensContainer {
      .pdf_container {
        justify-content: flex-start;
      }
    }
  }
}
