.PageImg {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transform: scale(0.722);
  flex-direction: column;
  p {
    margin: 20px auto;
    font-family: $title-font-family;
    font-size: 1.5rem;
    color: $main-color;
  }
}
@media screen and (min-width: 800px) {
  .PageImg {
    flex-direction: row;

    p {
      margin: 0px 30px;
    }
  }
}
