.presentation_home {
  width: 90%;
  margin: 50px auto;
  // background-image: url("../../assets/affiche_programme.jpg");
  // transform: perspective(1000px) rotateY(-40deg);

  // background-repeat: no-repeat;
  // background-position: right;
  // background-size: 50%;
  padding: 20px 0px;
  position: relative;

  img {
    position: absolute;
    z-index: -1;
    opacity: 0.9;
    transform: scale(0.7);
    // transform: perspective(1000px) rotateY(-40deg) scale(0.6);
    top: -15%;
  }

  h2 {
    text-align: center;
    margin-bottom: 30px;
    color: $title-color;
    // transform: perspective(-1000px) rotateY(50deg);
  }
  p {
    margin: 20px auto;
    text-align: justify;
    // padding: 10px;
    width: 90%;
    // background-color: rgba(255, 255, 255, 1);
    // box-shadow: 0 5px 10px rgba(14, 21, 47, 0.2),
    //   0 5px 10px rgba(14, 21, 47, 0.2);
    // transform: perspective(-1000px) rotateY(0deg);
  }
}

@media screen and (min-width: 800px) {
  .presentation_home {
    width: 70%;

    img {
      top: -50%;
      transform: perspective(1000px) rotateY(-40deg) scale(0.6);
      animation-fill-mode: both;
      opacity: 0;
      animation-duration: 0.8;
    }

    // h2 {
    //   transform: translateX(-20%);
    // }
    // // p {

    // }
  }
}

@keyframes apparitionAfficheintro {
  from {
    right: -50%;
    opacity: 0;
  }
  to {
    right: -20%;
    opacity: 0.8;
  }
}
