footer {
  width: 100%;
  text-align: center;
  margin: 20px auto 40px;

  h2 {
    font-size: 1.7rem;
  }

  .linkcontainer {
    display: flex;
    width: 100px;
    margin: auto;
    justify-content: center;
    align-items: center;
  }
  a {
    display: inline-block;

    &:nth-child(1) {
      margin-right: 20px;
    }
  }

  img {
    width: 25px;
    height: 25px;
    color: $main-color;
  }
}
