.faq_container {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 20px auto;

  .element_container {
    width: 100%;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    animation-duration: 0.8s;
    animation-fill-mode: both;
    transform: translateY(30px);

    h3 {
      width: 90%;
      margin-left: 10px;
      //   color: $title-color;
      color: $main-color;
    }

    p {
      padding: 5px;
      width: 90%;
      margin: 0px auto 0px 20px;
      text-align: justify;
    }

    .question_container {
      width: 80%;
      padding-top: 20px;
      margin: 20px auto;
      p {
        font-family: $title-font-family;
        font-size: 1.3rem;
      }
    }
    .response_container {
      width: 80%;
      margin: 20px auto;
      padding-bottom: 20px;
    }
    &:nth-child(even) {
      background-color: $third-background-color;
    }
  }
}

@media screen and (min-width: 800px) {
  .faq_container {
    .element_container {
      width: 100%;

      .question_container {
        width: 65%;
      }
      .response_container {
        width: 65%;
      }
    }
  }
}
@keyframes apparitionFaq {
  from {
    transform: translateY(30px);
  }
  to {
    transform: translateY(0px);
  }
}
