.presentation_membres {
  width: 100%;
  margin: 50px auto;
  display: flex;
  flex-direction: column;

  h2 {
    text-align: center;
    width: 80%;
    margin: 0px auto 40px auto;
    // color: rgb(9, 103, 66);
  }

  .membre_container {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin: auto;
    animation-duration: 0.8s;
    animation-fill-mode: both;

    .description {
      width: 100%;
      margin: 0px auto 40px;
      flex-direction: column;
      display: flex;
      justify-content: center;
      align-items: center;
      order: 2;
      position: relative;

      .faQuote {
        color: $third-background-color;
        display: block;
        // position: absolute;
      }
      // #quote1 {
      //   left: 10%;
      //   top: 40px;
      // }

      // #quote2 {
      //   right: 8%;
      //   bottom: -25px;
      // }

      p {
        text-align: justify;
        width: 80%;
      }
      h2 {
        color: $main-color;
      }
    }

    .img {
      width: 100%;
      display: flex;
      margin: 40px auto;
      flex-direction: column;
      justify-content: center;
      align-items: center;

      &:nth-child(odd) {
        order: 3;
      }
      &:nth-child(even) {
        order: 1;
      }

      h3 {
        // color: $title-color;
        color: $title-color;
      }
      h4 {
        font-family: "IM Fell English SC", serif;
      }

      img {
        margin: auto;
        background-size: 50% 200%;
        width: 150px;
        height: 150px;
        border-radius: 50%;
        animation-duration: 0.8s;
        animation-fill-mode: both;
      }
    }
    &:last-child {
      flex-direction: column;
      .description {
        width: 80%;
        order: 2;
      }
      .img {
        justify-content: space-around;
        flex-direction: column;
        order: 1;
        div {
          margin: 20px auto;
        }
        h3 {
          text-align: center;
        }
      }
    }
  }
  .membre_container:nth-child(even) {
    background-color: $third-background-color;
    transform: translateY(100px);

    .description {
      .faQuote {
        color: white;
      }
    }

    // h4 {
    //   color: white;
    // }
  }
}

@media screen and (min-width: 800px) {
  .presentation_membres {
    width: 100%;
    position: relative;
    justify-content: space-around;

    .membre_container {
      flex-direction: row;
      width: 100%;
      justify-content: space-around;
      padding: 20px;

      .description {
        // position: relative;
        width: 160%;
        // right: 5%;
        // transform: translateX(-8%);
      }
      .img {
        // position: relative;
        // transform: translateX(8%);
        width: 80%;
        // left: 8%;
      }
      &:last-child {
        flex-direction: column;
        .description {
          width: 80%;
          order: 2;
        }
        .img {
          justify-content: space-around;
          flex-direction: row;
          order: 1;
          h3 {
            text-align: center;
          }
        }
      }
    }
    .membre_container:nth-child(even) {
      .img {
        &:nth-child(even) {
          order: 3;
        }
      }
    }
    .membre_container:nth-of-type(1) {
      .img {
        &:nth-child(even) {
          order: 1;
        }
      }
    }
    .membre_container:nth-of-type(7) {
      .img {
        &:nth-child(even) {
          order: 1;
        }
      }
    }
  }
}

@keyframes apparitionMembre {
  from {
    transform: translateY(100px);
  }
  to {
    transform: translateY(0px);
  }
}
