.faq_container_Admin {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 50px auto;

  .element_container {
    width: 100%;
    margin: 0px auto;
    display: flex;
    flex-direction: column;
    animation-duration: 0.8s;
    animation-fill-mode: both;
    transform: translateY(30px);
    position: relative;

    h3 {
      width: 90%;
      margin-left: 10px;
      //   color: $title-color;
      color: $main-color;
    }

    p {
      padding: 5px;
      width: 90%;
      margin: 0px auto 0px 20px;
      text-align: justify;
    }

    .question_container {
      width: 80%;
      padding-top: 20px;
      margin: 20px auto;
      p {
        font-family: $title-font-family;
        font-size: 1.3rem;
      }
    }
    .response_container {
      width: 80%;
      margin: 20px auto;
      padding-bottom: 20px;
    }
    &:nth-child(even) {
      background-color: $third-background-color;
      button {
        background-color: white;
      }
    }
  }
  button {
    width: 100px;
    border-radius: 10px;
    border: none;
    background-color: $third-background-color;
    padding: 5px 10px;
    font-family: $title-font-family;
    color: $main-color;
    position: absolute;
    right: 20%;
    top: 10px;
    transition-duration: 0.3s;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);

    &:hover {
      transform: scale(1.05);
    }
  }
}

@media screen and (min-width: 800px) {
  .faq_container_Admin {
    .element_container {
      width: 100%;

      .question_container {
        width: 65%;
      }
      .response_container {
        width: 65%;
      }
    }
  }
}
@keyframes apparitionFaq {
  from {
    transform: translateY(30px);
  }
  to {
    transform: translateY(0px);
  }
}
