.mur_titre {
  width: 90%;
  margin: 130px auto 0px;
  h1 {
    text-align: center;
    color: $main-color;
    font-family: $title-font-family;
    font-size: 2.5rem;
  }
  p {
    width: 90%;
    margin: 20px auto;
    text-align: center;
  }
}
.vide {
  height: 100px;
}
.createFaq_container {
  width: 80%;
  margin: 30px auto 0px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  .formContainer {
    margin: 0px auto 30px;
    width: 80%;
    display: flex;
    flex-direction: column;
    padding: 20px;
    animation-duration: 0.8s;
    animation-fill-mode: both;
    animation-name: apparaitionContactForm;
    background-color: rgb(40, 40, 40);
    border-radius: 10px;
    max-width: 500px;
    position: relative;

    span {
      color: red;
      animation-name: ErrorMessage;
      animation-duration: 0.4s;
      text-align: center;
      margin-bottom: 3px;
    }
    .inputCreatePost {
      border: none;
      background-color: rgb(40, 40, 40);
      border-bottom: 1px solid $main-color;
      font-family: $main-font-family;
      color: white;
      margin: 0px auto 30px;
      padding: 10px;
      font-size: 1rem;
      width: 90%;

      &::placeholder {
        font-family: $main-font-family;
        font-size: 1.2rem;
        color: white;
      }
    }
    button {
      background-color: rgb(40, 40, 40);
      color: #ae952e;
      border: none;
      font-family: $title-font-family;
      font-size: 1.5rem;
    }
  }
}

@media screen and (min-width: 800px) {
  .createFaq_container {
    width: 50%;
  }
}

@keyframes apparaitionContactForm {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes DisparitionMessage {
  from {
    width: 100%;
  }
  to {
    width: 0px;
  }
}
@keyframes ErrorMessage {
  from {
    transform: translateX(0px);
  }
  33% {
    transform: translateX(15px);
  }
  66% {
    transform: translateX(-15px);
  }
  to {
    transform: translateX(0px);
  }
}
