.Postit_container {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  margin: 50px auto;
  flex-wrap: wrap;

  .element_container {
    width: 250px;
    height: 200px;
    margin: 20px auto;
    display: flex;
    background-color: $third-background-color;
    border-radius: 10px;
    flex-direction: column;
    animation-duration: 0.8s;
    animation-fill-mode: both;
    transform: translateY(30px);
    position: relative;

    h3 {
      width: 90%;
      margin-left: 5px;
      //   color: $title-color;
      color: $main-color;
    }

    p {
      padding: 5px;
      width: 100%;
      margin: 0px auto;
      text-align: justify;
    }

    .question_container {
      width: 90%;
      padding-top: 10px;
      height: 130px;
      margin: 0px auto;
      overflow: auto;

      p {
        font-family: $title-font-family;
        font-size: 1rem;
      }
    }
    .response_container {
      width: 90%;
      margin: 0px auto;
      position: absolute;
      bottom: 5px;
      left: 10px;
    }
    .like_container {
      position: absolute;
      display: flex;
      bottom: 5px;
      right: 15px;

      p {
        padding-bottom: 10px;
      }
      .fa {
        margin-top: 5px;
      }
    }
  }
  button {
    width: 100px;
    border-radius: 10px;
    border: none;
    background-color: white;
    padding: 5px 10px;
    font-family: $title-font-family;
    color: $main-color;
    position: absolute;
    right: 29%;
    top: 10px;
    transition-duration: 0.3s;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.7, 1, 0.7, 1);

    &:hover {
      transform: scale(1.05);
    }
  }
}

@media screen and (min-width: 800px) {
  .Postit_container {
    flex-direction: row;
    justify-content: start;

    .element_container {
      width: 250px;
      height: 200px;
      margin: 20px 30px;

      .question_container {
        width: 80%;
      }
      .response_container {
        width: 80%;
      }
    }
  }
}
@keyframes apparitionFaq {
  from {
    transform: translateY(30px);
  }
  to {
    transform: translateY(0px);
  }
}
