@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400&display=swap");

$font-1: "Poppins", sans-serif;

$color-1: rgb(20, 93, 136);
$color-2: rgb(30, 197, 184);
$color-3: rgb(30, 30, 30);
$color-4: rgb(32, 35, 42);

$second-background-color: rgb(30, 30, 30);
$third-background-color: rgb(241, 240, 239);
$second-color: #d45a29;
$main-color: rgb(168, 187, 228);
$title-color: rgb(9, 103, 66);
$texte-color: rgb(30, 30, 30);
// $main-font-family: Helvetica, Arial, sans-serif;
$main-font-family: "Roboto", sans-serif;

$background-color: white;
$title-font-family: "Spectral", serif;
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  margin: 0px;
  position: relative;
  background-color: $background-color;
  font-family: $main-font-family;
  color: $texte-color;
  position: relative;
  background-color: white;
}
li {
  list-style-type: none;
}
a {
  text-decoration: none;
  color: $color-3;
  cursor: pointer;
}
p {
  line-height: 1.5rem;
}
a:visited {
  color: $color-3;
}
h1,
h2,
h3,
h4 {
  font-family: $title-font-family;
}

.pointer {
  cursor: pointer;
}
