.liens_container {
  // margin: 100px auto;
  width: 100%;
  background-image: url("./../../assets/fondLiens.jpg");
  padding: 100px 0px 100px 0px;

  .sousContainer {
    background-color: rgba(241, 240, 239, 0.8);
    border-radius: 10px;

    width: 80%;
    margin: 20px auto;
    padding-bottom: 20px;
  }

  h1 {
    text-align: center;
    color: $title-color;
    margin: 10px auto 30px;
  }
  h2 {
    text-align: center;
    margin: 0px auto 30px;
    color: rgb(48, 73, 128);
  }
  .list_liens_container {
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: 0px auto 20px;
    position: relative;

    a {
      margin: 5px 0px;
      animation-duration: 0.8s;
      animation-fill-mode: both;
      animation-name: apparitionFaq;
      font-size: 1.3rem;
      font-weight: 600;
      font-family: $title-font-family;
      color: rgb(0, 0, 0);

      &:nth-child(2) {
        animation-delay: 0.3s;
      }
      &:nth-child(3) {
        animation-delay: 0.6s;
      }

      &::before {
        content: "";
        position: absolute;
        height: 7px;
        width: 7px;
        background-color: $main-color;
        left: -15px;
        margin-top: 7px;
        border-radius: 50%/50%;
      }

      &:hover {
        color: $title-color;
      }
    }
  }
  p {
    text-align: center;
    font-weight: 600;
    color: rgb(0, 0, 0);
  }
}

@media screen and (min-width: 800px) {
  .liens_container {
    .list_liens_container {
      width: 50%;
    }
  }
}

@keyframes apparitionFaq {
  from {
    transform: translateY(30px);
  }
  to {
    transform: translateY(0px);
  }
}
