.formulaire_container {
  margin: 150px auto 30px;
  width: 80%;
  display: flex;
  flex-direction: column;
  padding: 20px;
  animation-duration: 0.8s;
  animation-fill-mode: both;
  animation-name: apparaitionContactForm;
  background-color: rgb(40, 40, 40);
  border-radius: 10px;
  max-width: 500px;
  position: relative;

  h2 {
    text-align: center;
    color: $main-color;
    margin: 10px auto 30px;
  }

  span {
    color: red;
    animation-name: ErrorMessage;
    animation-duration: 0.4s;
    text-align: center;
    margin-bottom: 3px;
  }
  .input {
    border: none;
    background-color: rgb(40, 40, 40);
    border-bottom: 1px solid $main-color;
    font-family: $main-font-family;
    color: white;
    margin: 0px auto 30px;
    padding: 10px;
    font-size: 1rem;
    width: 90%;

    &::placeholder {
      font-family: $main-font-family;
      font-size: 1.2rem;
      color: white;
    }
  }
  button {
    background-color: rgb(40, 40, 40);
    color: #ae952e;
    border: none;
    font-family: $title-font-family;
    font-size: 1.5rem;
  }
  .message_envoyerContainer {
    position: absolute;
    bottom: 40%;
    left: 10%;
    width: 80%;
    background-color: rgba(115, 228, 100, 0.8);
    border-radius: 1px;
    animation-duration: 0.8s;
    animation-fill-mode: both;
    animation-name: apparaitionContactForm;
    p {
      padding: 30px 50px;
      text-align: center;
    }
    ::after {
      content: "";
      position: absolute;
      height: 5px;
      text-align: start;
      width: 100%;
      background-color: rgba(28, 131, 15, 0.8);
      left: 0px;
      bottom: 0px;
      animation-duration: 3s;
      animation-fill-mode: both;
      animation-timing-function: linear;
      animation-name: DisparitionMessage;
    }
  }
}

@media screen and (min-width: 800px) {
  .formulaire_container {
    width: 40%;
  }
}

@keyframes apparaitionContactForm {
  from {
    opacity: 0;
    transform: scale(0.5);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes DisparitionMessage {
  from {
    width: 100%;
  }
  to {
    width: 0px;
  }
}
@keyframes ErrorMessage {
  from {
    transform: translateX(0px);
  }
  33% {
    transform: translateX(15px);
  }
  66% {
    transform: translateX(-15px);
  }
  to {
    transform: translateX(0px);
  }
}
